*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
  font-size: 62.5%;
}

body {
  font-family: "Nunito", sans-serif;
  color: #6d5d4b;
  font-weight: 300;
  line-height: 1.6;
}

.container {
  display: grid;
  grid-template-rows: 80vh min-content repeat(4, min-content);
  grid-template-columns: [sidebar-start] 8rem [sidebar-end full-start] minmax(6rem, 1fr) [center-start] repeat(8, [col-start] minmax(min-content, 14rem) [col-end]) [center-end] minmax(6rem, 1fr) [full-end];
}

.heading-1 {
  font-family: "Josefin Sans", sans-serif;
  font-weight: 400;
}

.heading-2 {
  font-family: "Josefin Sans", sans-serif;
  font-weight: 400;
}

.heading-3 {
  font-family: "Josefin Sans", sans-serif;
  font-weight: 400;
}

.heading-4 {
  font-family: "Josefin Sans", sans-serif;
  font-weight: 400;
}

.heading-5 {
  font-family: "Josefin Sans", sans-serif;
  font-weight: 400;
}

.heading-6 {
  font-family: "Josefin Sans", sans-serif;
  font-weight: 400;
}

.heading-1 {
  font-size: 4.5rem;
  color: #f9f7f6;
  line-height: 1;
}

.heading-2 {
  font-size: 4rem;
  font-style: italic;
  line-height: 1;
}

.heading-2--light {
  color: #f9f7f6;
}

.heading-2--dark {
  color: #54483a;
}

.heading-3 {
  font-size: 1.6rem;
  color: #c69963;
  text-transform: uppercase;
}

.heading-4 {
  font-size: 1.9rem;
}

.heading-4--light {
  color: #f9f7f6;
}

.heading-4--dark {
  color: #54483a;
}

.btn {
  background-color: #c69963;
  color: #fff;
  border: none;
  border-radius: 0;
  font-family: "Josefin Sans", sans-serif;
  font-size: 1.5rem;
  text-transform: uppercase;
  cursor: pointer;
  padding: 1.8rem 3rem;
  transition: all 0.2s;
}

.btn:hover {
  background-color: #b28451;
}

.mb-sm {
  margin-bottom: 2rem;
}

.mb-md {
  margin-bottom: 3rem;
}

.mb-lg {
  margin-bottom: 4rem;
}

.mb-hg {
  margin-bottom: 8rem;
}

.sidebar {
  background-color: #c69963;
  grid-column: sidebar-start / sidebar-end;
  grid-row: 1 / -1;
  display: flex;
  justify-content: center;
}

.nav-btn {
  border-radius: 0;
  border: none;
  background-color: #fff;
  height: 2px;
  width: 4.5rem;
  margin-top: 4rem;
}

.nav-btn::before,
.nav-btn::after {
  content: "";
  background-color: #fff;
  height: 2px;
  width: 4.5rem;
  display: block;
}

.nav-btn::before {
  transform: translateY(-1.5rem);
}

.nav-btn::after {
  transform: translateY(1.3rem);
}

.header {
  padding: 8rem;
  padding-top: 4rem;
  background-color: #54483a;
  background-image: linear-gradient(rgba(16, 29, 44, 0.93), rgba(16, 29, 44, 0.93)), url("../img/hero.jpeg");
  background-size: cover;
  background-position: center;
  grid-column: full-start / col-end 6;
  display: grid;
  grid-template-rows: 1fr min-content minmax(6rem, min-content) 1fr;
  grid-template-columns: minmax(min-content, max-content);
  grid-row-gap: 1.5rem;
  justify-content: center;
}

.header__logo {
  height: 3rem;
  justify-self: center;
}

.header__btn {
  align-self: start;
  justify-self: start;
}

.header__seenon-text {
  display: grid;
  grid-template-columns: 1fr max-content 1fr;
  align-items: center;
  grid-column-gap: 1.5rem;
  font-size: 1.6rem;
  color: #aaa;
}

.header__seenon-text::before,
.header__seenon-text::after {
  content: "";
  height: 1px;
  display: block;
  background-color: currentColor;
}

.header__seenon-logos {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-column-gap: 3rem;
  justify-items: center;
  align-items: center;
}

.header__seenon-logos img {
  max-height: 2.5rem;
  max-width: 100%;
  filter: brightness(70%);
}

.realtors {
  background-color: #101d2c;
  grid-column: col-start 7 / full-end;
  padding: 3rem;
  display: grid;
  align-content: center;
  justify-content: center;
  grid-row-gap: 2rem;
  justify-items: center;
}

.realtors__list {
  display: grid;
  grid-template-columns: min-content max-content;
  grid-column-gap: 2rem;
  grid-row-gap: 5vh;
  align-items: center;
}

.realtors__img {
  width: 7rem;
  border-radius: 50%;
  display: block;
}

.realtors__sold {
  text-transform: uppercase;
  color: #aaa;
  margin-top: -3px;
}

.features {
  grid-column: center-start / center-end;
  margin: 15rem 0;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(15rem, 1fr));
  grid-template-rows: auto auto;
  grid-gap: 6rem;
  align-items: start;
}

.feature {
  display: grid;
  grid-template-rows: min-content min-content;
  grid-template-columns: min-content 1fr;
  grid-row-gap: 1.5rem;
  grid-column-gap: 2.5rem;
}

.feature__icon {
  fill: #c69963;
  width: 4.5rem;
  height: 4.5rem;
  grid-row: 1 / -1;
  transform: translateY(-1rem);
}

.feature__text {
  font-size: 1.7rem;
}

.story__pictures {
  background-color: #c69963;
  background-image: linear-gradient(rgba(198, 153, 99, 0.5), rgba(198, 153, 99, 0.5)), url("../img/back.jpg");
  background-size: cover;
  grid-column: full-start / col-end 4;
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  grid-template-rows: repeat(6, 1fr);
  align-items: center;
}

.story__img--1 {
  width: 100%;
  grid-row: 2 / 6;
  grid-column: 2 / 6;
  box-shadow: 0 2rem 5rem rgba(0, 0, 0, 0.1);
}

.story__img--2 {
  width: 115%;
  grid-row: 4 / 6;
  grid-column: 4 / 7;
  z-index: 20;
  box-shadow: 0 2rem 5rem rgba(0, 0, 0, 0.2);
}

.story__content {
  background-color: #f9f7f6;
  grid-column: col-start 5 / full-end;
  padding: 6rem 8vw;
  /*
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    */
  display: grid;
  align-content: center;
  justify-items: start;
}

.story__text {
  font-size: 1.5rem;
  font-style: italic;
  margin-bottom: 4rem;
}

.homes {
  grid-column: center-start / center-end;
  margin: 15rem 0;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(25rem, 1fr));
  grid-gap: 7rem;
}

.home {
  background-color: #f9f7f6;
  display: grid;
  grid-row-gap: 3.5rem;
  grid-template-columns: repeat(2, 1fr);
}

.home__img {
  width: 100%;
  grid-column: 1 / -1;
  grid-row: 1 /2;
  z-index: 1;
}

.home__like {
  grid-row: 1 /2;
  grid-column: 2 / 3;
  fill: #c69963;
  height: 2.5rem;
  width: 2.5rem;
  z-index: 2;
  justify-self: right;
  margin: 1rem;
}

.home__name {
  width: 80%;
  justify-self: center;
  align-self: end;
  grid-column: 1 / -1;
  grid-row: 1 /2;
  transform: translateY(50%);
  z-index: 2;
  font-family: "Josefin Sans", sans-serif;
  font-size: 1.6rem;
  text-align: center;
  padding: 1.25rem;
  background-color: #101d2c;
  color: #fff;
  font-weight: 400;
}

.home__btn {
  grid-column: 1 / -1;
}

.home__location,
.home__rooms {
  margin-top: 2.5rem;
}

.home__location,
.home__rooms,
.home__area,
.home__price {
  font-size: 1.5rem;
  margin-left: 2rem;
  display: flex;
  align-items: center;
}

.home__location > svg,
.home__rooms > svg,
.home__area > svg,
.home__price > svg {
  fill: #c69963;
  height: 2rem;
  width: 2rem;
  margin-right: 1rem;
}

.gallery {
  background-color: #f9f7f6;
  grid-column: full-start / full-end;
  display: grid;
  grid-template-columns: repeat(8, 1fr);
  grid-template-rows: repeat(7, 5vw);
  grid-gap: 1.5rem;
  padding: 1.5rem;
}

.gallery__item--1 {
  grid-row: 6 / span 2;
  grid-column: 8 / span 1;
}

.gallery__item--2 {
  grid-row: 1 / span 3;
  grid-column: 3 / span 3;
}

.gallery__item--3 {
  grid-row: 1 / span 2;
  grid-column: 6 / span 1;
}

.gallery__item--4 {
  grid-row: 1 / span 2;
  grid-column: 1 / span 2;
}

.gallery__item--5 {
  grid-row: 1 / span 2;
  grid-column: 7 / span 2;
}

.gallery__item--6 {
  grid-row: 3 / span 3;
  grid-column: 1 / span 2;
}

.gallery__item--7 {
  grid-row: 4 / span 2;
  grid-column: 3 / span 2;
}

.gallery__item--8 {
  grid-row: 4 / span 1;
  grid-column: 5 / span 1;
}

.gallery__item--9 {
  grid-row: 3 / span 2;
  grid-column: 6 / span 2;
}

.gallery__item--10 {
  grid-row: 3 / span 3;
  grid-column: 8 / -1;
}

.gallery__item--11 {
  grid-row: 6 / span 2;
  grid-column: 1 / span 1;
}

.gallery__item--12 {
  grid-row: 6 / span 2;
  grid-column: 2 / span 2;
}

.gallery__item--13 {
  grid-row: 6 / span 2;
  grid-column: 4 / span 1;
}

.gallery__item--14 {
  grid-row: 5 / span 3;
  grid-column: 5 / span 3;
}

.gallery__img {
  width: 100%;
  object-fit: cover;
  height: 100%;
  display: block;
}

.footer {
  background-color: #101d2c;
  grid-column: full-start / full-end;
  padding: 8rem;
}

.nav {
  list-style: none;
  display: grid;
  grid-gap: 2rem;
  grid-template-columns: repeat(auto-fit, minmax(15rem, 1fr));
  align-items: center;
}

.nav__link:link,
.nav__link:visited {
  font-size: 1.4rem;
  padding: 1.5rem;
  display: block;
  color: #fff;
  text-decoration: none;
  font-family: "Josefin Sans", sans-serif;
  text-transform: uppercase;
  text-align: center;
  transition: all 0.3s ease;
}

.nav__link:hover,
.nav__link:active {
  background-color: rgba(255, 255, 255, 0.05);
  transform: translateY(-3px);
}

.copyright {
  font-size: 1.4rem;
  color: #aaa;
  margin-top: 6rem;
  text-align: center;
  width: 70%;
  margin-left: auto;
  margin-right: auto;
}

@media only screen and (max-width: 75em) {
  html {
    font-size: 50%;
  }
}

@media only screen and (max-width: 50em) {
  .container {
    grid-template-rows: 6rem calc(100vh - 6rem) repeat(6, min-content) !important;
  }

  .header {
    grid-column: 1 / -1;
  }

  .realtors {
    grid-column: 1 / -1;
  }

  .realtors__list {
    grid-template-columns: repeat(3, min-content max-content);
  }

  .story__pictures {
    grid-column: 1 / -1;
    padding: 6rem;
  }

  .story__img--1 {
    grid-column: 1 / 5;
    grid-row: 1/-1;
    height: auto;
    width: 100%;
  }

  .story__img--2 {
    width: 100%;
    grid-row: 1/-1;
  }

  .story__content {
    grid-column: 1 / -1;
    grid-row: 5 / 6;
  }
}

@media only screen and (max-width: 62.5em) {
  .container {
    grid-template-rows: 6rem 80vh repeat(5, min-content);
    grid-template-columns: [full-start] minmax(6rem, 1fr) [center-start] repeat(8, [col-start] minmax(min-content, 14rem) [col-end]) [center-end] minmax(6rem, 1fr) [full-end];
  }

  .sidebar {
    grid-column: 1 / -1;
    grid-row: 1 / 2;
    align-items: center;
    justify-content: flex-end;
  }

  .nav-btn {
    margin-top: 0;
    margin-right: 3rem;
  }

  .nav-btn::before {
    transform: translateY(-1.2rem);
  }

  .nav-btn::after {
    transform: translateY(1rem);
  }
}

@media only screen and (max-width: 37.5em) {
  .header {
    padding: 5rem;
  }

  .realtors__list {
    grid-template-columns: min-content max-content;
  }
}